import React, { Suspense, lazy } from 'react';
import Loader from './components/Loader';

const Main = lazy(() => {
  return Promise.all([
    import("./Main"),
    new Promise(resolve => setTimeout(resolve, 2000))
  ]).then(([moduleExports]) => moduleExports);
});

function App() {
  return (
    <div>
      <Suspense fallback={<Loader/>}>
        <Main />
      </Suspense>
    </div>
  );
}

export default App;
